import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

export default {
  namespaced: true,
  state: {
    scene: null,
    camera: null,
    renderer: null,
    controls: null
  },
  actions: {
    init({ state }, container) {
      return new Promise((resolve, reject) => {
        if (!container) reject();
        // SCENE
        state.scene = new THREE.Scene();
        // CAMERA
        var SCREEN_WIDTH = window.innerWidth,
          SCREEN_HEIGHT = window.innerHeight;
        /* RENDERER */
        state.renderer = new THREE.WebGLRenderer({ antialias: true });
        state.renderer.setSize(SCREEN_WIDTH, SCREEN_HEIGHT);
        /* LIGHT */
        var light1 = new THREE.PointLight(0xffffff);
        light1.position.set(0, 0, 250);
        var light2 = new THREE.PointLight(0xffffff);
        light2.position.set(0, 0, -250);
        state.scene.add(light1);
        state.scene.add(light2);
        // bgcolor
        state.renderer.setClearColor(0xcccccc, 1);
        container.appendChild(state.renderer.domElement);
        resolve();
      });
    },
    resetCameraforSF({ state }, { xMax, yMax, zMax }) {
      if (!state.camera) {
        var SCREEN_WIDTH = window.innerWidth,
          SCREEN_HEIGHT = window.innerHeight;
        var VIEW_ANGLE = 45,
          ASPECT = SCREEN_WIDTH / SCREEN_HEIGHT,
          NEAR = 0.1,
          FAR = 20000;
        state.camera = new THREE.PerspectiveCamera(
          VIEW_ANGLE,
          ASPECT,
          NEAR,
          FAR
        );
        state.scene.add(state.camera);
      }
      // CAMERA
      state.camera.position.set(
        1.5 * xMax,
        1.5 * yMax,
        zMax < 2 ? 4 * zMax : 4
      );
      state.camera.up = new THREE.Vector3(0, 0, 1);
      state.camera.lookAt(state.scene.position);

      state.controls = new OrbitControls(
        state.camera,
        state.renderer.domElement
      );
    }
  }
};
