<template>
  <div>
    <v-app-bar
      :collapse="true"
      :collapse-on-scroll="false"
      absolute
      color="deep-purple accent-4"
      dark
    >
      <v-menu
        v-model="menufunc"
        :close-on-content-click="false"
        :close-on-click="false"
        nudge-width="210"
        max-width="210"
        offset-y
      >
        <template v-slot:activator="{ on }">
          <v-app-bar-nav-icon v-on="on"></v-app-bar-nav-icon>
        </template>

        <v-card color="grey lighten-2">
          <v-row justify="center">
            <v-expansion-panels accordion>
              <v-expansion-panel>
                <v-card color="blue" elevation="3" dark>
                  <v-expansion-panel-header hide-actions class="py-0">
                    <template v-slot:default="{ open }">
                      Función
                      <v-spacer></v-spacer>
                      <div class="text-right">
                        <v-icon v-if="open">
                          mdi-arrow-up-drop-circle
                        </v-icon>
                        <v-icon v-else>
                          mdi-arrow-down-drop-circle
                        </v-icon>
                      </div>
                    </template>
                  </v-expansion-panel-header>
                </v-card>
                <v-expansion-panel-content>
                  <v-card class="mt-2" flat>
                    <v-form v-model="validfunction">
                      <v-row>
                        <v-col cols="12" class="pt-2 pb-0">
                          <v-text-field
                            label="z = "
                            placeholder="f(x,y)"
                            dense
                            v-model="zfunction"
                            :rules="functionRules"
                            :hint="function_message"
                            persistent-hint
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-form>
                  </v-card>
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel>
                <v-card color="blue" elevation="3" dark>
                  <v-expansion-panel-header hide-actions class="py-0">
                    <template v-slot:default="{ open }">
                      Variables
                      <v-spacer></v-spacer>
                      <div class="text-right">
                        <v-icon v-if="open">
                          mdi-arrow-up-drop-circle
                        </v-icon>
                        <v-icon v-else>
                          mdi-arrow-down-drop-circle
                        </v-icon>
                      </div>
                    </template>
                  </v-expansion-panel-header>
                </v-card>
                <v-expansion-panel-content>
                  <v-card class="mt-2" flat>
                    <v-row justify="space-around">
                      <v-col cols="12" class="pb-0">
                        <v-slider
                          v-model="xMinslider"
                          class="align-center"
                          max="0"
                          min="-20"
                          label="Min"
                          thumb-label="always"
                        >
                        </v-slider>
                      </v-col>
                      <v-col cols="12" class="pb-0">
                        <v-slider
                          v-model="xMaxslider"
                          class="align-center"
                          max="20"
                          min="0"
                          label="Max"
                          thumb-label="always"
                        >
                        </v-slider>
                      </v-col>
                    </v-row>
                  </v-card>
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel>
                <v-card color="blue" elevation="3" dark>
                  <v-expansion-panel-header hide-actions class="py-0">
                    <template v-slot:default="{ open }">
                      Parámetros
                      <v-spacer></v-spacer>
                      <div class="text-right">
                        <v-icon v-if="open">
                          mdi-arrow-up-drop-circle
                        </v-icon>
                        <v-icon v-else>
                          mdi-arrow-down-drop-circle
                        </v-icon>
                      </div>
                    </template>
                  </v-expansion-panel-header>
                </v-card>
                <v-expansion-panel-content>
                  <v-card class="mt-2" flat>
                    <v-card-text></v-card-text>
                  </v-card>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
            <v-card-actions>
              <v-row class="text-center">
                <v-col cols="12" class="py-0">
                  <v-btn
                    color="primary"
                    @click="replaceGraph"
                    :disabled="!validfunction"
                    small
                    width="200"
                  >
                    <v-icon size="16" class="mr-1">mdi-function</v-icon>
                    Graficar
                  </v-btn>
                </v-col>
                <v-col cols="12" class="pt-1 pb-0">
                  <v-btn
                    color="secondary"
                    @click="resetMainCamera"
                    small
                    width="200"
                  >
                    <v-icon size="16" class="mr-1">mdi-camera</v-icon>
                    Reiniciar camara
                  </v-btn>
                </v-col>
              </v-row>
            </v-card-actions>
          </v-row>
        </v-card>
      </v-menu>
    </v-app-bar>
    <div id="graph" style="position: absolute; height: 100%; left:0px; top:0px">
      <v-btn @click="goback" color="red" small dark fixed bottom left fab>
        <v-icon>mdi-keyboard-return</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<style></style>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { create, all } from "mathjs";
import router from "../router";

const config = {};
const math = create(all, config);

export default {
  data() {
    return {
      container: null,
      menufunc: false,
      zfunction: "",
      function_message: "",
      validfunction: true,
      functionRules: [
        f => !!f || "La función es requerida",
        f => this.validFunction(f) || "Función invalida"
      ],
      xMinslider: -10,
      xMaxslider: 10
    };
  },
  computed: {
    ...mapState("singleFunction", [
      "zFuncText",
      "segments",
      "xMin",
      "xMax",
      "yMin",
      "yMax",
      "zMin",
      "zMax",
      "wireMaterial",
      "plane3D",
      "graphMesh"
    ]),
    ...mapState("scene3D", ["scene", "camera", "renderer", "controls"])
  },
  watch: {
    xMinslider(newvalue) {
      this.setXMin(newvalue);
      this.updateGraph(this.scene);
    },
    xMaxslider(newvalue) {
      this.setXMax(newvalue);
      this.updateGraph(this.scene);
    },
    xMin(newvalue) {
      this.xMinslider = newvalue;
    },
    xMax(newvalue) {
      this.xMaxslider = newvalue;
    }
  },
  mounted() {
    this.container = document.getElementById("graph");
    // WIREMATERIAL FOR GRAPHMESH
    this.createDefaultWireMaterial();

    this.init(this.container).then(() => {
      // ADD AXES AND PLANE
      this.createPlane3D(this.scene);
      // ADD THE MAIN GRAPH TO SCENE
      this.zfunction = this.zFuncText;
      this.setFirstGraphtoScene(this.scene)
        .then(() => {
          this.resetCameraforSF({
            xMax: this.xMax,
            yMax: this.yMax,
            zMax: this.zMax
          });
          this.xMinslider = this.xMin;
          this.xMaxslider = this.xMax;
          this.animate();
        })
        .catch(error => {
          alert(error);
          this.resetCameraforSF({ xMax: 10, yMax: 10, zMax: 1 });
          this.animate();
        });
    });
  },
  methods: {
    ...mapMutations("singleFunction", [
      "setZFuncText",
      "setOneParameter",
      "setXMin",
      "setXMax",
      "createDefaultWireMaterial"
    ]),
    ...mapActions("singleFunction", [
      "setFirstGraphtoScene",
      "createGraph",
      "updateGraph",
      "createPlane3D"
    ]),
    ...mapActions("scene3D", ["init", "resetCameraforSF"]),
    animate() {
      requestAnimationFrame(this.animate);
      this.controls.update();

      this.renderer.render(this.scene, this.camera);
    },
    goback() {
      // go back by one record, the same as history.back()
      router.go(-1);
    },
    replaceGraph() {
      this.setZFuncText(this.zfunction);
      this.createGraph(this.scene).then(() => {
        this.function_message = "Gráfico listo!";
        setTimeout(() => (this.function_message = ""), 3000);
      });
    },
    resetMainCamera() {
      this.resetCameraforSF({
        xMax: this.xMax,
        yMax: this.yMax,
        zMax: this.zMax
      });
    },
    validFunction(functiontext) {
      try {
        const parser = math.parser();
        parser.evaluate("f(x,y) = ".concat(functiontext)); // f(x, y)
        parser.set("a", 1);
        parser.set("b", 1);
        parser.set("c", 1);
        parser.set("d", 1);
        parser.set("e", 1);
        parser.evaluate("f(1,1)");
        return true;
      } catch (error) {
        return false;
      }
    }
  }
};
</script>
